import mapboxgl from "mapbox-gl";

export class MapboxGLWrapper {
    public map: mapboxgl.Map
    constructor(id: string) {

        this.initMap(id);
    }
    private initMap(id: string) {
        // https://account.mapbox.com
        mapboxgl.accessToken = process.env.REACT_APP_MAPBOX;
        this.map = new mapboxgl.Map({
            container: id,
            zoom: 15,
            // minZoom: 10,
            center: [-71.093161, 42.358871],
            style: 'mapbox://styles/mapbox/dark-v10' // 'mapbox://styles/mapbox/streets-v11'
        });
        this.map.dragRotate.disable();

        // https://docs.mapbox.com/mapbox-gl-js/api/events/#mapmouseevent#type
        this.map.on('mousemove', (e) => { this.mouseMove(e); });
        this.map.on('click', (e) => { this.mouseClick(e); });
        this.map.on('mousedown', (e) => { this.mouseDown(e); });
        this.map.on('dblclick', (e) => { this.mouseDClick(e); });
        this.map.on('mouseup', (e) => { this.mouseUp(e); });

        let div = document.getElementsByClassName('mapboxgl-canvas')[0] as HTMLDivElement;
        div.style.position = 'absolute';
        
        div = document.getElementsByClassName('mapboxgl-control-container')[0] as HTMLDivElement;
        div.style.display = 'none';

    }
    public center = (lng: number = -99.292649, lat: number = 39.043903) => {
        // const v = new mapboxgl.LngLat(lon, lat);
        // console.log(v);
        this.map.setCenter({ lat, lng })
    }
    public toMap = (x: number, y: number) => {
        const p = this.map.unproject({x, y} as any)
        return [p.lng, p.lat];
    }
    public toScreen = (lon: number, lat: number) => {
        const p = this.map.project([lon, lat])
        return [p.x, p.y];
    }
    public mouseDown = (e) => {
        // console.log(this.toScreen(-71.093161, 42.358871))
        // console.log(JSON.stringify(e.point), e.lngLat.wrap())
    }
    public mouseUp = (e) => {
        // console.log(JSON.stringify(e.point), e.lngLat.wrap())
    }
    public mouseMove = (e) => {
        // console.log(JSON.stringify(e.point), e.lngLat.wrap())
    }
    public mouseClick = (e) => {
        // console.log(e)
        
        // console.log(this.toMap(100, 100));
        // console.log(e.point)
        // this.center()
        // this.map.zoomTo(0)
        // console.log(JSON.stringify(e.point), e.lngLat.wrap())
    }
    public mouseDClick = (e) => {
        // console.log(JSON.stringify(e.point), e.lngLat.wrap())
    }
}


